import clsx from 'clsx';
import {createContext, useContext} from 'react';
import {Button, ButtonProps, ButtonVariant} from '..';
import {TeaserBodyProps, TeaserDescriptionProps, TeaserHeadlineProps, TeaserImageProps, TeaserProps, TeaserTaglineProps} from './teaser.interface';

const TeaserContext = createContext({backgroundColor: 'dark'});
export const useTeaserContext = () => useContext(TeaserContext);

const Teaser = ({backgroundColor = 'dark', imageAlignment = 'left', children}: TeaserProps) => {

    const containerClass     = backgroundColor === 'dark' ? 'zep-bg-background-dark zep-text-typography-light-100' : 'zep-bg-background-light zep-text-typography-dark-100';
    const flexDirectionClass = imageAlignment === 'left' ? 'md:zep-flex-row' : 'md:zep-flex-row-reverse';

    return (
        <TeaserContext.Provider value={{backgroundColor}}>
            <div
                className={clsx(flexDirectionClass, containerClass, 'zep-flex-col', 'zep-rounded-lg', 'zep-shadow-lg', 'md:zep-flex')}
                data-testid='zep-teaser'
            >
                {children}
            </div>
        </TeaserContext.Provider>
    );
}
const TeaserImage = ({src, alt, aspectRatio = '16:9'}: TeaserImageProps) => {
    const imageAspectRation = aspectRatio === '16:9' ? 'zep-pb-[calc(9_*_100%_/_16)]' : 'zep-pb-[calc(16_*_100%_/_9)]'
    return (
        <div data-testid='zep-teaser-image' className={clsx('zep-justify-center zep-flex zep-items-center md:zep-w-5/6 md:zep-relative xl:zep-w-10/12')}>
            <div className={clsx('zep-relative', 'zep-w-full', 'zep-h-0', 'md:zep-relative', 'md:zep-block', 'md:zep-pl-8', 'md:zep-h-0', imageAspectRation)}>
                <div className={clsx('zep-absolute zep-inset-0 zep-justify-center zep-flex zep-items-center')}>
                    <img alt={alt} src={src} className={clsx('zep-w-full zep-h-full zep-object-cover md:zep-w-full md:zep-h-full md:zep-max-w-full')}/>
                </div>
            </div>
        </div>
    )
}

const TeaserBody = ({children}: TeaserBodyProps) => {
    return (
        <div data-testid='zep-teaser-body' className={clsx('zep-@container zep-flex zep-items-center zep-p-1.5 md:zep-px-1.5 md:zep-w-1/2 xl:zep-w-1/2 lg:zep-px-[80px]')}>
            <div className='lg:zep-pl-8 sm:zep-block zep-inline-grid'>
                {children}
            </div>
        </div>
    )
}

const TeaserTagline = ({children}: TeaserTaglineProps) => {
    return (
        <p data-testid='zep-teaser-tagline' className={clsx('zep-typography-tagline zep-mb-0.5')}>
            {children}
        </p>
    );
}

const TeaserHeadline = ({children}: TeaserHeadlineProps) => {
    return (
        <h4 data-testid='zep-teaser-headline' className={clsx(
            'zep-typography-headlineLG',
            'supports-cqi:zep-typography-headlineLG-fluid-cqi',
            'supports-cqi:after:zep-text-[0.23em]',
            'zep-mb-0.5',
            'zep-text-left')}>
            {children}
        </h4>
    );
}

const TeaserDescription = ({children}: TeaserDescriptionProps) => {
    return (
        <p data-testid='zep-teaser-description'>{typeof children === 'string' ? `${children.slice(0, 256)}${children.length > 256 ? '...' : ''}` : ''}</p>
    )
}

const TeaserButton = ({children, label, onClick}: Pick<ButtonProps, 'children' | 'onClick' | 'icon' | 'iconPosition' | 'label'>) => {
    const {backgroundColor} = useTeaserContext()
    const buttonVariant     = backgroundColor === 'dark' ? ButtonVariant.LightPrimary : ButtonVariant.DarkPrimary;
    return (
        <Button
            variant={buttonVariant}
            onClick={onClick}
            className={clsx('zep-mt-1 lg:zep-mt-1.5 md:zep-flex')}
            data-testid='zep-teaser-button'
            label={label}
        > {children}</Button>
    );
}

Teaser.Image       = TeaserImage;
Teaser.Tagline     = TeaserTagline;
Teaser.Body        = TeaserBody;
Teaser.Headline    = TeaserHeadline;
Teaser.Description = TeaserDescription;
Teaser.Button      = TeaserButton;


export {Teaser};